.login-window {
  padding: 1.15em;

  .welcome-text {
    text-align: center;
    font-size: 1em;
    padding: 0.2em .2em 2em .2em; }

  .indicate-text {
    text-align: center;
    padding: 1em 0 2.1em;
    font-size: 0.8em; }

  .login-form {
    display: flex;
    flex-direction: column; }

  .login-form-block {
    display: block;
    margin-bottom: 1.1em;

    &__label {
      font-size: 1.16em; } }

  .login-form-block__input,
  .login-form-block__select {
    margin: .5em 0 0.2em;
    width: 100%;
    font-size: 1.2em;
    padding: 0.375em 0.5em; }

  .login-form-block__input {
    color: #525252;
    border: 1px solid gainsboro;
    border-radius: 1px;
    &__error {
      border: 1px solid #de0a23;
      outline: none; } }

  .login-form-block__errorMessage {
    color: #de0a23;
    font-size: 0.81em;
    position: relative;
    display: block;
    padding-left: 18px;
    &:before {
      content: '';
      display: block;
      position: absolute;
      width: 15px;
      height: 15px;
      left: 0;
      background-image: url('../../assets/img/error-15x15.png');
      background-repeat: no-repeat; } }


  .login-form-block__select {
    background-color: transparent;
    cursor: pointer;
    appearance: none; }
  .login-form-block__pseudoElem {
    position: relative;
    &:before {
      content: "";
      display: block;
      background: transparent;
      position: absolute;
      right: 9px;
      top: 40px;
      width: 8px;
      height: 8px;
      border: {
        right: 2px solid  #525252;
        bottom: 2px solid  #525252; }
      transform: rotate(45deg);
      cursor: pointer; } }

  .login-form-button {
    display: block;
    color: #fff;
    font-size: 1em;
    margin: 1.6em auto 0;
    text-transform: uppercase;
    border-radius: 2px;
    padding: .7em 3.5em;
    border: none;
    cursor: pointer;
    background-color: #b80635;
    &:disabled {
      background-color: #eee;
      color: #000; } } }
